<template lang="html">
  <div id="main">
    <Viz :sortedKeywords='keywords'
      :svg='map'
      :entries='entries'
      />
    <Title
      :title='title'
      :credits='credits'
      />
  </div>
</template>

<script>
import Title from './Title.vue'
import Venn from './Venn.vue'
import Viz from './Viz.vue'
// import Menu from './components/Menu.vue'
// import Questions from './components/Questions.vue'

import { keywords as energyKeywords, entries as energyEntries } from '../../../output/energy.json'
import { keywords as wellBeingKeywords, entries as wellBeingEntries } from '../../../output/well-being.json'

export default {
  props : ["info", "map"],
  data() {
    return {
      introPlayed: false
    }
  },
  computed : {
    keywords() {
      const rawKeywords = this.map === 'energy' ? energyKeywords : wellBeingKeywords
      const sortedKeywords = rawKeywords.sort((a,b) => a[1]+b[1])
      return sortedKeywords
    },
    entries() {
      return this.map === 'energy' ? energyEntries : wellBeingEntries
    },
    title() { return  this.info.title },
    subtitle() { return this.info.subtitle },
    credits() { return this.info.credits },
    vennTextA() { return this.info.vennTextA },
    vennTextB() { return this.info.vennTextB },

  },
  methods: {
    skipIntro() {
      this.introPlayed= true
    },
    // selectMenuItem(i) {
    //   this.selectedMenuItem = i
    // }
  },
  components: {
    // Menu,
    // Questions,
    Title,
    Venn,
    Viz
  }
}
</script>

