<template lang="html">
  <div id="references" v-if="!!refs.length">
    <div class="header">
      <a href="javascript:void(0)"
        class="btn close"
        @click="hideRefs"
        v-if="closable"
        >
        ×
      </a>
      <h3>
        <a href="javascript:void(0)"
          @click="showRefs"
          v-if="!!refs.length"
          >
          References
          <span v-if="refCount">
            ({{refCount}})
          </span>
        </a>
      </h3>
    </div>
    <div id="ref-list" v-if="open">
      <hr>
      <ul>
        <li v-for="ref in refs" :key='ref.keyword'>
          <h4>{{ref.keyword}}
            <small>({{ref.entries.length}})</small>
          </h4>
          <ul>
            <li v-for="(entry, i) in ref.entries" :key='`ref-${ref.keyword}-${i}`'>
              <p v-html='entry'></p>
            </li>
          </ul>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  props : {
    refs : {
      type: Array
    },
    open : {
      type: Boolean,
      default: false
    }
  },
  methods :{
    hideRefs() {
      this.$emit('toggleRefs', false)
    },
    showRefs() {
      this.$emit('toggleRefs', true)
    }
  },
  computed : {
    closable() {
      return this.open && !!this.refs.length
    },
    refCount() {
      return this.refs.map(r => r.entries.length).reduce((p,c) => p + c, 0)
    }
  }
}
</script>

