<template>
  <div id="viz">
      <a href="javascript:void(0)"
        class="btn toggle-keywords-handle"
        @click="handleToggleDrawer"
        >
        {{toggleListBtnText}}
      </a>
    <div class="viz-shapes">
      <Shapes
        :svg='svg'
        @selectKeyword="selectKeyword"
        :keywords="keywords"
        :highlightedKeyword="highlightedKeyword"
        :selectedKeywords="selectedKeywords"
        @highlightKeyword="highlightKeyword"
        />
    </div>
    <div v-if="showKeywords" class="drawer">
      <a href="javascript:void(0)"
        class="btn close"
        @click="handleToggleDrawer"
        >
          ×
      </a>
      <KeywordsList
        :keywords="keywordsWithCount"
        :highlightedKeyword="highlightedKeyword"
        :selectedKeywords="selectedKeywords"
        @clearKeywords="clearKeywords"
        @selectKeyword="selectKeyword"
        @highlightKeyword="highlightKeyword"
        />
    </div>
    <References
      :open="showRefs"
      @toggleRefs="toggleRefs"
      :refs="bib"
    />
  </div>
</template>

<script>
import KeywordsList from './KeywordsList.vue'
import References from './References.vue'
import Shapes from './Shapes.vue'

export default {
  name: 'viz',
  props: ["sortedKeywords", "entries", "svg"],
  data() {
    return {
      introPlayed: true,
      keywordsWithCount : this.sortedKeywords,
      keywords: this.sortedKeywords.map(d => d[0]),
      selectedKeywords : [],
      selectedEntries : [],
      showKeywords: false,
      showRefs: false,
      highlightedKeyword: null
    }
  },
  methods: {
    handleToggleDrawer() {
      this.showKeywords = !this.showKeywords
    },
    toggleRefs(refState) {
      this.showRefs = refState || !this.showRefs
    },
    highlightKeyword(keyword, e){
      this.highlightedKeyword = keyword
    },
    getEntries(keyword) {
      return this.entries.filter(d => d.keywords.includes(keyword))
        .map(d => d.bib)
    },
    selectKeyword(keyword, e){
      if(e) e.preventDefault()
      // add/remove from selection
      if(!this.selectedKeywords.includes(keyword)) {
        this.selectedKeywords.push(keyword)
      } else {
        const i = this.selectedKeywords.indexOf(keyword)
        if (i > -1) this.selectedKeywords.splice(i, 1);
      }
    },
    clearKeywords(e) {
      if(e) e.preventDefault()
      console.log("clear");
      this.selectedKeywords = []
    }
  },
  computed : {
    gridded() {
      return this.showKeywords ? "grid" : null
    },
    toggleListBtnText() {
      return this.showKeywords ? "Keywords" : "Keywords"
    },
    bib() {
      return this.selectedKeywords.map(keyword => {
        const entries = this.getEntries(keyword)
        return {
          keyword,
          entries
        }
      })
    }
  },
  components: {
    KeywordsList,
    References,
    Shapes
  }
}
</script>

