  text-align: center;
}

h3 {
  margin-top: 0
}
.grid {
  display: grid;
  grid-template-columns: 2fr 5fr 1fr;
  grid-column-gap: 0px;
}

.main {
  border: 1px dashed #ccc;
  width: 100%;
  height: 100vh
}

.drawer {
  top:0;
  position: fixed;
  left:0;
  width: 250px;
  background: white;
  height: 100vh;
  border-right: 1px dashed #555;
}

.toggle-keywords-handle {
  position: fixed;
  width: 80px;
  border-radius: 10px;
  text-align: center;
  padding: .5em 1em;
  background: #555;
  font-size: 1em;
  color: white;
  text-decoration: none;
  top: 20px;
  left: 20px
}

a, a:visited {
  text-decoration: none;
  color: #333
}

li a.highlighted {
  background-color: LightYellow
}

li a.selected {
  background-color: yellow
}
</style>
