<template lang="html">
  <div ref="svg-container" :id="`map-${svg}`">
    <EnergySvg v-if="svg ==='energy'"
      @mouseover='handleMouseOver'
      @mouseout='handleMouseOut'
      @click="handleClick"
      />

    <WellBeingSvg v-if="svg ==='wellBeing'"
      @mouseover='handleMouseOver'
      @mouseout='handleMouseOut'
      @click="handleClick"
      />
  </div>
</template>

<script>
import EnergySvg from "../assets/Energy image-Dec19.svg?inline";
import WellBeingSvg from "../assets/Wellbeing.svg?inline";

export default {
  name: "Shapes",
  props: {
    keywords : {
      type: Array
    },
    selectedKeywords : {
      type: Array
    },
    highlightedKeyword : {
      type: String
    },
    svg: {}
  },
  data() {
    return {
      axeOver:false,
      wordsShown: false
    }
  },
  mounted() {

    const svgns = "http://www.w3.org/2000/svg";

    const svg = this.$refs["svg-container"]
      .getElementsByTagName('svg')[0]

    const forbiddenValues = ['', 'words', 'shapes', 'axes', 'captions', 'titles', 'plus', 'minus ', 'energy']

    Array.from(svg.getElementsByTagName("g"))
      .map(g => {
        if( !forbiddenValues.includes(g.id) ) {
          const bbox = g.getBBox()
          const rect = document.createElementNS(svgns, 'rect');
          rect.setAttributeNS(null, 'x', bbox.x);
          rect.setAttributeNS(null, 'y', bbox.y);
          rect.setAttributeNS(null, 'height', bbox.height);
          rect.setAttributeNS(null, 'width', bbox.width);
          rect.setAttributeNS(null, 'fill', 'transparent');
          // rect.setAttributeNS(null, 'fill', '#'+Math.round(0xffffff * Math.random()).toString(16));
          g.appendChild(rect);
        }
      })

  },
  methods : {
    handleMouseOver(e) {

      const isWord = document.querySelector("#words")
        .contains(e.target)

      const g = e.target.parentNode

      const isAxe = ["productivity-axe", "wellbeing-axe"].includes(g.id)

      if(isAxe && !this.wordsShown) {
        this.axeOver = true
        document.querySelector("#map-wellBeing #captions").style.visibility = "visible";
        document.querySelector("#map-wellBeing #captions").style.opacity = 1;
      }


      if (g.tagName === 'g') {
        const keyword = g.getAttribute("data-name") || g.id

        if (keyword) {
          const q = `g[data-name="${keyword}"], g#${keyword}`
          const els = document.querySelectorAll(q)

          els.forEach( el => el.classList.toggle('highlighted'))

          this.$emit('highlightKeyword', keyword)
        }
      }
    },
    handleMouseOut(e) {
      if (this.axeOver && !this.wordsShown) {
        document.querySelector("#map-wellBeing #captions").style.visibility = "hidden";
        document.querySelector("#map-wellBeing #captions").style.opacity = 0;
        this.axeOver = false
      }

      const g = e.target.parentNode

      if (g.tagName === 'g') {
        const keyword = g.getAttribute("data-name") || g.id

        if (keyword) {

          const q = `g[data-name="${keyword}"], g#${keyword}`
          const els = document.querySelectorAll(q)

          els.forEach( el => el.classList.toggle('highlighted'))

          this.$emit('highlightKeyword', keyword)
          this.$emit('highlightKeyword', null)
        }
      }
    },
    handleClick(e) {

      const g = e.target.parentNode

      const isAxe = ["productivity-axe", "wellbeing-axe"].includes(g.id)

      if(isAxe) {
        const visibility = !this.wordsShown ? "visible" : "hidden";
        const opacity = !this.wordsShown ? 1 : 0;

        document.querySelector("#map-wellBeing #words").style.visibility = visibility
        document.querySelector("#map-wellBeing #words").style.opacity = opacity

        document.querySelector("#map-wellBeing #arrows").style.visibility = visibility
        document.querySelector("#map-wellBeing #arrows").style.opacity = opacity

        document.querySelector("#map-wellBeing #captions").style.visibility = visibility
        document.querySelector("#map-wellBeing #captions").style.opacity = opacity

        this.wordsShown = !this.wordsShown
      }

      if (g.tagName === 'g' && !isAxe) {
        const keyword = g.getAttribute("data-name") || g.id
        if (keyword) {
          g.classList.toggle('selected');
          this.$emit('selectKeyword', keyword)
        }
      }
    }
  },
  components : {
    EnergySvg,
    WellBeingSvg
  }
}
</script>

