  }

  #axes #productivity-axe.highlighted #plus,
  #axes #productivity-axe.highlighted #plus-2 line,
  #axes #productivity-axe.highlighted #minus line {
    stroke: white!important;
  }

  #axes #wellbeing-axe.highlighted line,
