  #axes #wellbeing-axe.highlighted path#eye-3,
  #axes #wellbeing-axe.highlighted path#eye-4 {
    fill: #81CA56!important;
  }

  #axes #wellbeing-axe.highlighted #wellbeing_text path,
  #axes #wellbeing-axe.highlighted #wellbeing_titles path
  {
