<template lang="html">
  <div id="keywords">
    <h3>
      Keywords
    </h3>
    <p>
      <a href="#"
      class="clear btn"
      @click="clearKeywords()"
        >Clear selection
      </a>
    </p>
    <ul>
      <li v-for="keyword in keywords">
        <a href="#"
          @click="selectKeyword(keyword[0])"
          @mouseover="highlightKeyword(keyword[0])"
          :class="`${keyword[0] === highlightedKeyword ?
            'highlighted' : null } ${selectedKeywords.includes(keyword[0]) ?
              'selected' : null }`"
          >
          {{ keyword[0] }} <small>({{ keyword[1] }})</small>
        </a>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    keywords : {
      type: Array
    },
    selectedKeywords : {
      type: Array
    },
    highlightedKeyword : {
      type: String
    }
  },
  methods: {
    selectKeyword(keyword) {
      this.$emit('selectKeyword', keyword)
    },
    highlightKeyword(keyword) {
      this.$emit('highlightKeyword', keyword)
    },
    clearKeywords() {
      this.$emit('clearKeywords')
    }
  }
}
</script>

