<template>
  <div class="menu">
    <div class="menu-item" v-for="(q, i) in menuItems">
      <router-link :to="q.link">
        <svg :width="`${
          highlighted === i || currentRouteName === q.name
          ? 35 : 25
        }`" height="50" xmlns="http://www.w3.org/2000/svg">
          <g @mouseover='handleMouseOver(i)' @mouseout='handleMouseOut(i)'>
            <rect
              :transform='`translate(25,0) rotate(45)`'
              x="0" y="0" width="35" height="35"
              :style='{
                fillOpacity : `${
                  currentRouteName === q.name
                    ? 1 : .5
                  }`,
                fill : `${
                  currentRouteName === q.name
                    ? "black" : "black"
                  }`
                }'
              />
          </g>
        </svg>
      </router-link>
      <span v-if="highlighted === i">{{q.name}}</span>
    </div>

  </div>
</template>

<script>
export default {
  name: "menuBrowse",
  props: ["menuItems", "selectedMenuItem"],
  data() {
    return {
      highlighted: null
    }
  },
  methods: {
    handleMouseOver(i) {
      this.highlighted = i
    },
    handleMouseOut(i) {
      this.highlighted = null
    }
    // handleClick(i) {
    //   this.$emit('selectMenuItem', i)
    // }
  },
  computed: {
    currentRouteName() {
        return this.$route.name;
    }
  }
}
</script>

